import $ from 'ZEPTO_PATH';
var VARIABLE = require('UNIT_PATH/variable'); 
var model = require('UNIT_PATH/model');
var view = require('UNIT_PATH/view');

function hot() {
	if(!$('.hot').length) return;
	var hotId = $('.hot').attr('data-hot-id');
	var _href = $('.hot').find('.con a').attr('href');

	$('.hot .like').click(function() {
		var _this = $(this);

		model.hotLike(_this, function(data) {
			view.hotLikeView(_this, data);
		});
	});

	$('.hot .share').click(function() {
		model.hotShare(hotId);
		window.location.href = _href;
		return false;
	});

	$('.hot .con a').click(function() {
		model.hotLook(hotId);
		window.location.href = _href;
		return false;
	});

	model.getLikeStatus(hotId, function(data) {
		view.hotLikeStatusRender($('.hot .like'), data);
	});
}

module.exports = hot;


