import $ from 'ZEPTO_PATH';
import Loading from 'UNIT_PATH/loading';
import Remind from 'UNIT_PATH/remind';

var VARIABLE = require('UNIT_PATH/variable'); 
var Model = require('UNIT_PATH/model');
var View = require('UNIT_PATH/view');

function main() {
    // loading
    Loading.showLoading({
        type: 'load-fullscreen',
        loadingTxt: '数据获取中..'
    }, function(_loadingHtml) {
        $('.red-page').append(_loadingHtml);
    });

    // 获取优惠券状态
    getCouponStatus();

    // 领取优惠券事件绑定
    couponEventBind();
}

function getCouponStatus() {
    Model.getCouponInfo(function(data) {
        View.renderCouponsStatus(data);
        getAllBtnStyle();
    });
}

function getAllBtnStyle() {
    $('#coupons-wrap .act-item').each(function() {
        var item = $(this).find('.coupon.can-get');

        if(!item.length) {
            $(this).find('.btn-get-coupons').removeClass('btn-get-coupons').addClass('btn-geted-coupons').text('去使用');
        }
    });
}

function couponEventBind() {
    var btns = $('.coupon');

    // 单独领取
    btns.click(function() {
        var _this = this;

        if($(_this).find('.coll-load').length) return;

        if($(_this).hasClass('can-get')) {
            Loading.showLoading({
                type: 'coll-load',
                loadingTxt: '领取中..'
            }, function(_loadingHtml) {
                $('.loading-mask', $(_this)).append(_loadingHtml).show();
            });

            Model.getCoupon(_this, function(data) {
                View.renderCouponsGet(_this, data);
                $(_this).find('.coll-load').remove();
                $(_this).find('.loading-mask').hide();
                getAllBtnStyle();
            });
        }else if($(_this).hasClass('dry-up')) {
            Remind._normalRemind('已抢光');
        }
    });

    // 一键领取
    $('.act-title-wrap').click(function(ev) {
        var target = $(ev.target);

        if(target.closest('.btn-get-coupons').length) {
            var item = target.closest('.btn-get-coupons').closest('.act-item');

            Model.getAll(item, function(data) {
                View.renderGetAll(target.closest('.btn-get-coupons'), item, data);
            });
            return false;
        }
    });
}

module.exports = main;


